.booksort{
    width: 1200px;
    height: 350px;
    margin: 0 auto;
    margin-top: 29px;
    // background-color: pink;
}
.search_tab{
    width: 100%;
    height: 36px;
    // background-color: deeppink;
    border-bottom: 2px solid #ff2328;
    padding-right: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    a{
        color: #969696;
        margin-left: 5px;
    }
    .search_btn{
        width: 124px;
        height: 36px;
        line-height: 36px;
        background-color: #ff2328;
        text-align: center;
        color: #fff;
        font-size: 16px;
        
    }
}
.crumbs_fb{
    height: 32px;
    line-height: 32px;
    background-color: #f5f5f5;
    width: 1180px;
    margin: 0 auto;
    padding: 6px 0 6px 20px;
    span{
        color: #ff2328;
    }
}
.filter_box{
    margin-bottom: 20px;
    ul{
        border-top: 2px solid #fff;
        width: 1200px;
        background-color: #fef8f5;
        li{
            border-bottom: 1px dotted #eeeceb;
            width: 1200px;
            height: 36px;
        }
        .firstli{
            background: #fbf2ed;
            border-bottom: 1px solid #eeeceb;
        }
    }
}
.filter_box {
    .listleft{
        width: 71px;
        height: 18px;
        float: left;
        padding: 9px 0px 0px 12px;
        color: #999;
        line-height: 18px;
    }
    .listright{
        width: 1080px;
        float: left;
        padding: 4px 2px 4px 35px;
        position: relative;
        background-color: #fff;
        span{
            float: left;
            height: 22px;
            width: 125px;
            padding: 3px 10px 3px 0;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            a{
                padding: 0px 19px 0 5px;
                line-height: 20px;
                height: 20px;
                border: 1px solid #fff;
                color: #555;
                padding-right: 0;
                margin-right: 19px;
            }
            a:hover{
                color: #fd5115;
                text-decoration: underline;
            }
        }
    }
}
.tools_box{
    height: 30px;
    border:  1px solid #efefef;
    line-height: 30px;
    background-color: #f5f5f5;
    .sorting_box{
        height: 30px;
        float: left;
        li:nth-of-type(1) a{
            color: #fd5115;
        }
        li{
            background: url(../img/tools_box_bg.png);
            float: left;
            background-position: right -97px;
            cursor: pointer;
            margin-left: -1px;
            background-repeat: no-repeat;
            height: 30px;
            a{
                text-align: center;
                padding: 2px 10px 0;
                line-height: 28px;
            }
            .icon{
                background: url(../img/tools_box_bg.png);
                display: inline-block;
                height: 11px;
                width: 7px;
                background-position: 0px -195px;
                margin-left: 5px;
                overflow: hidden;
                vertical-align: middle;
            }
            .spe{
                background: url(http://search.dangdang.com/Standard/Search/Extend/hosts/images/model/vsearch/btn_new.gif) 0 0 no-repeat;
            }
            
        }
        
        li:hover{
            background: #eeeceb;
        }
        li:hover a{
            color: #fd5115;
        }
        li:hover .icon{
            background-position: 25px -195px;
        }
        .price:hover .spe{
            background-position: 0 0;
        }
        .sprice_area{
            float: right;
            width: 115px;
            .text{
                float:none;
                border: solid 1px #e1e1e1;
                padding: 2px 0 2px 13px;
                margin: 0;
                line-height: 18px;
                height: 18px;
                width: 34px;
                color: #878787;
                outline: none;
                background: #fff url(../img/tools_box_bg.png) -45px -169px no-repeat;
            }
        }
    }
}
.search_top{
    width: 1200px;
    background-color: #fff;
    position: fixed;
    left: 50%;
    top: 0;
    margin-left: -600px;
    border: 1px solid #efefef;
    border-top: none;
    box-shadow: 0 0 6px 0 #949292;
}
.button{
    line-height: 30px;
    float: left;
    width: 340px;
    height: 30px;
    overflow: hidden;
    margin-left: 10px;
    padding: 1px 0px 0 11px;
    position: relative;
    cursor: pointer;
    li{
        float: left;
        padding: 8px 17px 8px 0;
        height: 14px;
        line-height: 14px;
        vertical-align: middle;
        position: relative;
        a{
            background: url(../img/tools_box_bg.png);
            display: inline-block;
            height: 14px;
            line-height: 14px;
            background-position: 0 0;
            background-repeat: no-repeat;
            padding-left: 18px;
            text-decoration: none;
            color: #555;
        }
    }
}
.search{
    float: left;
    width: 356px;
    height: 27px;
    border: 1px solid #c8c8c8;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.search .wrap{
    width: 250px;
    /* height: 30px; */
    position: relative;
}
.search .wrap .result{
    width: 250px;
    position: absolute;
    left: -2px;
    top: 37px;
    z-index: 10000;
    display: none;
}
.wrap .result ul{
    background: #fff;
    border: 1px solid #c8c8c8;
    border-top: none;
    text-indent: 10px;
}
.wrap .result ul li{
    height: 22px;
    line-height: 22px;
    cursor: pointer;
}
.wrap .result ul li a{
    color: #323232;
}
.wrap .result ul li:hover a{
    color: #ff2832;
}
.search input{
    width: 235px;
    border: none;
    outline: none;
    height: 100%;
    text-indent: 10px;
}
.search button{
    width: 76px;
    height: 100%;
    background-color: #f6f6f6;
    border: none;
    color: #969696;
    font-size: 14px;
}
.search .btn{
    width: 30px;
    height: 27px;
    border: none;
    background: url(http://img63.ddimg.cn/upload_img/00459/home/head_sprite.png) no-repeat;
    background-position: -9px -354px;
}

.search_wrap{
    width: 1200px;
    // height: 1000px;
    margin: 0 auto;
    // background-color: pink;
    padding: 20px 0 0 0;
}
.search_wrapcon{
    width: 1200px;
    ul{
        width: 100%;
    }
    li{
        width: 100%;
        height: 242px;
        // background-color: deeppink;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 20px;
        box-sizing: border-box;
        transition: all .2s ease-in-out;
        .leftimg{
            width: 200px;
            height: 200px;
            img{
                display: block;
                width: 200px;
                height: 200px;
            }
        }
        .rightcon{
            width: 926px;
        }
    }
    li:hover{
        background-color: #f8f8f8;
    }
}
.rightcon{
    .title{
        width: 926px;
        height: 34px;
        overflow: hidden;
        font-size: 14px;
        a{
            line-height: 34px;
            color: #1a66b3;
        }
        a:hover{
            color: #fd5115;
            text-decoration: underline;
        }
        span{
            color: #ff2328;
        }
        line-height: 20px;
    }
    .price{
        line-height: 30px;
        color: #ff2328;
        font-size: 18px;
        font-weight: bold;
    }
    .author{
        line-height: 30px;
    }
    .details{
        line-height: 20px;
        height: 40px;
        overflow: hidden;
        color: #333;
        font-size: 14px;
        margin-bottom: 5px;
    }
    .start{
        span{
            background: url(http://search.dangdang.com/Standard/Search/Extend/hosts/images/model/vsearch/icon_star3.png) repeat-x;
            width: 76px;
            height: 16px;
            line-height: 16px;
            display: inline-block;
            font-size: 0;
        }
    }
    .brand{
        span{
            background-color: white;
            border: 1px solid #ff2832;
            color: #ff2832;
            border-radius: 2px;
            padding: 0 2px;
            font-size: 12px;
            margin-right: 5px;
            display: inline-block;
            white-space: nowrap;
            margin-bottom: 4px;
            line-height: 14px;
        }
    }
    .join_car{
        width: 100px;
        height: 28px;
        line-height: 28px;
        color: #fff;
        text-align: center;
        background-color: #ff2328;
        border-radius: 3px;
        font-size: 14px;
        cursor: pointer;
    }
}
 

.page_btn{
    width: 500px;
    height: 40px;
    margin: 50px auto;
    li{
        width: 38px;
        height: 38px;
        line-height: 40px;;
        text-align: center;
        font-size: 18px;
        border: 1px solid #e7e7e9;
        float: left;
        cursor: pointer;
        margin-left: -1px;
    }
    li:hover{
        background-color: #fd5115;
        border-color: #fd5115;
        color: #fff;
    }
    .dott:hover{
        background-color: #fff;
        border-color: #e7e7e9;
        color: #333;
        cursor: default;
    }
    .jiantou{
        background-color: #fff;
        border-color: #e7e7e9;
        color: #fd5115;
    }
    .jiantou:hover{
        @extend .jiantou
    }
    li.active{
        background-color: #fd5115;
        border-color: #fd5115;
        color: #fff;
        cursor: default;
    }
}